
.item-filter[data-v-5a4b7db2] {
  clear: both;
  overflow: auto;
  padding: 40px 0 0px 0;
  background: url(/static/gaming-pattern.png);
}
.item-filter-container[data-v-5a4b7db2] {
  margin: auto;
  max-width: 1500px;
}
.filter[data-v-5a4b7db2] {
  width: 22%;
  border-radius: 32px;
  color: white;
  float: left;
  margin: 0px 4% 4% 0;
  padding: 10px;
  box-sizing: border-box;
  background: #E3350D;
  cursor: pointer;
  box-shadow: 4px 4px black;
  transition: all .3s;
}
.filter[data-v-5a4b7db2]:hover {
    background: #464a4c;
    color: white;
}
.filter[data-v-5a4b7db2]:nth-child(4n) {
    margin: 0 0 4% 0;
}
@media screen and (max-width: 1350px) {
.item-filter-container[data-v-5a4b7db2] {
    max-width: calc(100% - 220px);
}
}
@media screen and (max-width: 700px) {
.item-filter-container[data-v-5a4b7db2] {
    max-width: calc(100% - 40px);
}
.filter-container[data-v-5a4b7db2] {
    display: block;
}
.filter[data-v-5a4b7db2] {
    width: 100%;
    margin: 10px 0;
}
}



